
html {
    width: 100%;
    height: 100%;
    display: table;
    font-family:"Museo Sans";
    letter-spacing: 1px;
    font-size: 1rem;
    padding: 0px;
}

body {
    width: 100%;
    height: 100%;
    margin: 0px;
}

#root{
    width: 100%;
    height: 100%;
}

a{
    text-decoration: none;
    color: inherit;
}

@font-face {
    font-family:"Museo Sans";
    src: url(/static/media/museoSans.5b9453f7.ttf) format("TrueType");
  }

@media(min-width: 651px) {
    .normalNav{ display: inline-block};
    .link{ display: inline-block };
    .hamburgerMenu{ display: none; };
}

@media(max-width: 650px) {
    .link{ display: none }
    .normalNav{ display: none; };
    .hamburgerMenu{ display: inline-block; }
}

/* PACKAGES */
.p1{ background: #f6f7f1; }
.p2{ background: #02cccc }
.p3{ background: #7b49ec; color: #ffffff}
.p4{ background: #2c2a6c; color: #ffffff}
.App{
    height: 100%;
}
/* BURGER MENU */

/* Position and sizing of burger button */

.hamburgerMenu{
    position: fixed;
  }
  
  
  .bm-burger-button {
      position: relative;
      width: 36px;
      height: 30px;
      margin-right: 5%;
      right: 60%;
      outline: none;
      border: none;
    }
  
    @media(min-width: 651px) {
      .bm-burger-button{ display: none; }
  }
  
  
    /* Color/shape of burger icon bars */
    .bm-burger-bars {
      background: #373a47;
      -webkit-transition: all .15s ease-out;
      transition: all .15s ease-out;
    }
     
    /* Color/shape of burger icon bars on hover*/
    .bm-burger-bars-hover {
      background-color: #feda6a;
    }
     
  
    /* Color/shape of close button cross */
    .bm-cross {
      background-color: #feda6a;
      width: 25px;
    }
     
    /*
    Sidebar wrapper styles
    Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
    */
    .bm-menu-wrap {
      position: fixed;
      height: 100%;
    }
     
    /* General sidebar styles */
    .bm-menu {
      background: #373a47;
      padding: 2.5em 1.5em 0;
      font-size: 1.15em;
      margin-top: -14%;
      display: flex;
      justify-content: center;
      border-left: #d4d4dc solid 1px;
    }
     
     
    /* Wrapper for item list */
    .bm-item-list {
      color: #b8b7ad;
      padding: 0.8em;
    }
     
    /* Individual item */
    .bm-item {
      display: inline-block;
      margin: 20%;
      margin-left: 0;
      margin-right: 0;
    }
     
    /* Styling of overlay */
    .bm-overlay {
      background: rgba(0, 0, 0, 0.2);
      margin-left: 31%;
  
    }
    @media(min-width: 651px) {
      .bm-overlay{ display: none; }
  }
  
    .menu-item{
        color: #d4d4dc;
        text-decoration: none;
        outline: none;
        -webkit-transition: all .3s ease-out;
        transition: all .3s ease-out;
    }
  
    .menu-item:hover{
      color:#879FFF;
    }
